<template>
  <div>
    <b-form @submit="onSubmit"
      @reset="onReset"
      v-if="show">
      <b-form-group id="input-group-1"
        label="聯絡Email:"
        label-for="input-1"
        description="作為聯絡使用">
        <b-form-input id="input-1"
          v-model="form.email"
          type="email"
          required
          placeholder="Enter email"></b-form-input>
      </b-form-group>

      <b-form-group id="input-group-2"
        label="聯絡人姓名:"
        label-for="input-2">
        <b-form-input id="input-2"
          v-model="form.name"
          required
          placeholder="Enter name"></b-form-input>
      </b-form-group>

      <b-form-group id="input-group-3"
        label="幾位毛孩:"
        label-for="input-3">
        <b-form-select id="input-3"
          v-model="form.number"
          :options="numbers"
          required></b-form-select>
      </b-form-group>
      <template v-for="(_, key) in form.number">
        <ChildFileUpload :key="key"
          :number="key"
          @methodChild="methodChild" />
      </template>
      <b-button type="submit"
        variant="primary">提交</b-button>
      &nbsp;
      <b-button type="reset"
        variant="danger">清除</b-button>
    </b-form>

    <h3 v-else>感謝填寫，稍後會與您聯絡</h3>
    <!-- <b-card class="mt-3" header="Form Data Result">
      <pre class="m-0">{{ form }}</pre>
    </b-card> -->
  </div>
</template>

<script>
import ChildFileUpload from "./ChildFileUpload.vue";
export default {
  name: "BookingForm",
  components: {
    ChildFileUpload
  },
  // props: ["callbackBooked"],
  data() {
    return {
      form: {
        email: "",
        name: "",
        number: 1,
        child: []
      },
      numbers: [1, 2, 3, 4, 5],
      show: true,

      name: "",
      nameState: null,
      submittedNames: []
    };
  },
  watch: {},
  created() {
    this.form = {
      email: "a@c.c",
      name: "aaaa",
      number: 1
    };
  },
  methods: {
    onSubmit(evt) {
      evt.preventDefault();
      // alert(JSON.stringify(this.form))
      this.axios
        .post("http://localhost:8000/books.php", this.form, {
          headers: { "Content-Type": "application/x-www-form-urlencoded" }
        })
        .then(result => {
          // console.log("SUCCESS!!", result);
          if (result.data.status !== true) return;
          // console.info("callback");
          // this.$emit("callbackBooked");
        })
        .catch(err => {
          console.log("FAILURE!!", err);
        });
    },
    onReset(evt) {
      evt.preventDefault();
      // Reset our form values
      this.form.email = "";
      this.form.name = "";
      this.form.number = 0;
      this.form.child = [];
      // Trick to reset/clear native browser form validation state
      this.show = false;
      this.$nextTick(() => {
        this.show = true;
        this.form.number = 1;
      });
    },
    methodChild(data, idx) {
      console.info(
        "%c data, idx ",
        "background-color:#bf360c;color:#fff",
        data,
        idx
      );
      this.form.child[idx] = data;
    },
    checkFormValidity() {
      const valid = this.$refs.form.checkValidity();
      this.nameState = valid;
      return valid;
    },
    resetModal() {
      this.name = "";
      this.nameState = null;
    },
    handleOk(bvModalEvt) {
      // Prevent modal from closing
      bvModalEvt.preventDefault();
      // Trigger submit handler
      this.handleSubmit();
    },
    handleSubmit() {
      // Exit when the form isn't valid
      if (!this.checkFormValidity()) {
        return;
      }
      // Push the name to submitted names
      this.submittedNames.push(this.name);
      // Hide the modal manually
      this.$nextTick(() => {
        this.$bvModal.hide("modal-prevent-closing");
      });
    }
  }
};
</script>
