<template>
  <div id="app">
    <div class="one pic"
      :class="`pic${navId}`"></div>
    <div class="content">
      <h1>我想聽你說</h1>
      <HomeMenu :navId="navId"
        :menus="menus"
        @changeId="changeNavId" />
      <!-- 說明 -->
      <component :is="compList"></component>
    </div>
  </div>
</template>

<script>
// import HelloWorld from './components/HelloWorld.vue'
import HomeMenu from "./components/HomeMenu";

import HomePage from "./pages/Home";
import Communication from "./pages/Communication";
import EnergyPage from "./pages/Energy";
import HealingPage from "./pages/Healing";
// import FbMixin from "./fbMixin";

export default {
  name: "App",
  // mixins: [FbMixin],
  components: {
    HomeMenu,

    HomePage,
    Communication,
    EnergyPage,
    HealingPage
    // HelloWorld
  },
  data: function() {
    return {
      navId: 0,
      menuArr: [
        {
          name: "Home",
          componentName: "HomePage"
        },
        {
          name: "動物溝通",
          componentName: "Communication"
        },
        {
          name: "超微技巧",
          componentName: "EnergyPage"
        },
        {
          name: "能量療愈",
          componentName: "HealingPage"
        }
      ]
    };
  },
  computed: {
    compList() {
      const list = ["HomePage", "Communication", "EnergyPage", "HealingPage"];
      return list[this.navId] ?? "HomePage";
    },
    menus() {
      return this.menuArr.map(item => item.name);
    }
  },
  created() {},
  methods: {
    changeNavId(id) {
      this.navId = id;
    }
  }
};
</script>

