<template>
  <div class="file-upload">
    <b-row>
      <template v-for="(input, ids) in rowInput">
        <b-col :key="`xxx-${ids}`">
          <b-form-group
            :label="input.text"
            :id="`${input.name}${ids}${number}`"
          >
            <b-form-select
              v-if="input.options"
              :id="`${input.name}${ids}${number}`"
              v-model="inputForm[input.modal]"
              onChange
              required
              :options="input.options"
              :placeholder="input.placeholder"
            ></b-form-select>
            <b-form-input
              v-else
              :id="`${input.name}${ids}${number}`"
              v-model="inputForm[input.modal]"
              required
              :placeholder="input.placeholder"
            ></b-form-input>
          </b-form-group>
        </b-col>
      </template>
    </b-row>
    <b-form-group :id="`input-photo-${number}`">
      <b-form-file
        v-model="file"
        placeholder="毛小孩清晰照, 可用拖拉照片至此上傳"
        @input="methodChange"
        drop-placeholder="Drop file here..."
      ></b-form-file>
    </b-form-group>
  </div>
</template>

<script>
export default {
  name: "ChildFileUpload",
  props: ["number", "methodChild"],
  data() {
    return {
      file: null,
      inputForm: {
        file: "",
        name: "",
        sex: "",
        old: "",
        ligation: "",
      },
      rowInput: [
        {
          name: "input-child-name-",
          modal: "name",
          text: "名稱",
          placeholder: "毛小孩的名字",
          value: "",
        },
        {
          name: "input-child-sex-",
          modal: "sex",
          text: "性別",
          placeholder: "毛小孩的姓別",
          options: ["男孩", "女孩"],
          value: "",
        },
        {
          name: "input-child-old-",
          modal: "old",
          text: "年齡",
          placeholder: "毛小孩的年齡",
          options: Array.from(Array(20).keys()),
          value: "",
        },
        {
          name: "input-child-ligation-",
          modal: "ligation",
          text: "結紮狀態",
          placeholder: "毛小孩的是否已經結紮？",
          options: ["未結紮", "已結紮"],
          value: "",
        },
      ],
    };
  },
  watch: {
    inputForm: {
      handler(data) {
        const isEmpty = Object.keys(data).find((item) => data[item] === "");
        if (isEmpty === undefined) {
          this.methodChange(data);
        }
      },
      immediate: true,
      deep: true,
    },
    file(data) {
      if (!data) return;
      this.fileUpload(data);
    },
  },
  created() {
    // this.inputForm = {
    //   file: "upload/lakjdsflakjdf.jpg",
    //   name: "bb",
    //   sex: "男孩",
    //   old: "3",
    //   ligation: "已結紮",
    // };
    // this.methodChange(this.inputForm);
  },
  methods: {
    fileUpload(data) {
      let formData = new FormData();
      formData.append("photo", data);
      this.axios
        .post("http://localhost:8000/file_upload.php", formData, {
          headers: { "Content-Type": "multipart/form-data" },
        })
        .then((result) => {
          // console.log('SUCCESS!!', result);
          if (result.data.status !== true) return;
          this.inputForm.file = result.data.file;
        })
        .catch((err) => {
          console.log("FAILURE!!", err);
        });
    },
    methodChange(data) {
      this.$emit("methodChild", data, this.number);
    },
  },
};
</script>

<style>
.file-upload {
  margin-top: 1rem;
}
</style>
